// base
// =======================================================

@import "../scss/functions"; // Functions
@import "../scss/variables"; // Variables
@import "../scss/mixins"; // Mixins

@import "../scss/base";
@import "../scss/ui-fontsize";
@import "../scss/ui-dialog";
@import "../scss/ui-toast";
@import "../scss/icon-full";
@import "../scss/icon";
@import "../scss/ui-border1px";
@import "../scss/ui-progress";

.ui-list-text li{
	width: 60%;
	height: 45px;
	line-height: 45px;
	text-align: center;
	margin: 10px 0;
}
.ui-new {
	@include border(#0ff)
}

.ui-center{
	@include border(#0f0);
	.ui-btn{
		text-align: center;
		padding: 10px;
	}
}
.icon_lists {
  @include clearfix;
}
.icon_lists li{
  float:left;
  width: 100px;
  height:180px;
  text-align: center;
}
.icon_lists .icon{
  font-size: 42px;
  line-height: 100px;
  margin: 10px 0;
  color:#333;
  -webkit-transition: font-size 0.25s ease-out 0s;
  -moz-transition: font-size 0.25s ease-out 0s;
  transition: font-size 0.25s ease-out 0s;

}
.icon_lists .icon:hover{
  font-size: 100px;
}

.iconfont {
  font-family:"iconfont" !important;
  font-size:16px;
  font-style:normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
}

.icon_lists .ui-icon-more:before{
	color: #0f0;
}
// --progress

.demo-block{
  @include progress(#f00, #0ff);
}
