// Component:		  Variables
//
// Description:		Define all variables
//
// =============================================================================

// -----------------------------------------------------------------------------
// Global Variables
// -----------------------------------------------------------------------------

// Set classes namespace, e.g. `am-`
$namespace: 	  ui- !default;
// set components namespace
$ui-dialog:     #{$namespace}dialog !default;
$ui-progress:   #{$namespace}progress  !default;
$ui-border:     #{$namespace}border !default;
$ui-icon: 		  #{$namespace}icon !default;
$ui-poptoast: 	  #{$namespace}poptoast !default;
// This sets 1rem to be 16px
$rem-base: 						        10px;

$base-font-size: 				      100% !default;

$base-line-height: 			      1.5 !default;

// Global
// ------------------------------------------------
$global-padding: 				      1rem !default;
$global-margin: 				      1rem !default;
$global-spacing: 				      rem-calc(15) !default;

//browser set
// color
// -----------------------------------------------
$black: 						          #000;
$white: 						          #fff;
			
$global-primary: 				      #2980b9;
$global-secondary: 			      #3498db;
$global-success: 				      #00c486;
$global-warning: 				      #F2D12A;
$global-alert: 					      #e74c3c;

$global-background-color: 		#ffa;
$global-border: 				      #F2F2F2;
//compontents color default
$retina-border-color: 			  #c4c4c4;

// Gray
// -----------------------------------------------

$gray-lighter: 					      lighten($black, 93.5%); // #eee
$gray-light: 					        #ddd;
$gray: 							          #bbb;
$gray-dark: 					        #666;
$gray-darker: 					      lighten($black, 20%); // #333

// Link color
// -----------------------------------------------

$link-color: 					        $global-primary;
$link-hover-color: 				    darken($link-color, 15%);

// Base color

$base-background: 				    $white;
$base-mark-background: 			  $global-background-color;
$base-mark-color: 				    $gray-dark;

// Base
// ----------------------------------------------

$global-font-size: 				    1.6rem;

$base-font-weight: 				    normal;
$base-text-color: 				    $gray-dark;
$base-margin-vertical: 			  1.6rem;

$base-heading-font-weight: 		600;
$base-heading-margin-top: 		2em;

// hr
$base-hr-border: 				      $gray-lighter;

// Blockquote
$base-blockquote-border: 		  $global-border;
$base-blockquote-small-color: $gray-light;

// Ins
$base-ins-background: 			  $global-background-color;
$base-ins-color: 				      $gray-dark;

// fonts size
// ------------------------------------------------------------------------------
//$font-size-xs: rem-calc(12) !default;
//$font-size-sm: rem-calc(14) !default;
//$font-size-default: rem-calc(16) !default;
//$font-size-lg: rem-calc(18) !default;
//$font-size-xl: rem-calc(20) !default;
//$font-size-xxl: rem-calc(24) !default;

// font
// ----------------------------------------------

$base-font-family: 		  "Segoe UI", "Lucida Grande", "Helvetica Neue", Helvetica, Arial, "Microsoft YaHei", FreeSans, Arimo, "Droid Sans", "wenquanyi micro hei", "Hiragino Sans GB", "Hiragino Sans GB W3", "FontAwesome", sans-serif;
$font-family-monospace: Monaco, Menlo, Consolas, "Courier New", "FontAwesome", monospace;
$font-family-kai: 		  Georgia, "Times New Roman", Times, Kai, "Kaiti SC", KaiTi, BiauKai, "FontAwesome", serif;

// TODO: ä¿®æ¹ åªä½æ¥è¯¢
// Breakpoint
// -----------------------------------------------------------------------------

$breakpoint-small: 		  640px; // 0 - 640px
$breakpoint-medium: 	  1024px; // 641 - 1024px
$breakpoint-large: 		  1280px; // 1025 - 1280px

$breakpoint-small-max: 	$breakpoint-small;
$breakpoint-md: 		    $breakpoint-small-max; // legacy var, DO NOT USE IT

$breakpoint-medium-min: ($breakpoint-small + 1);
$breakpoint-medium-max: $breakpoint-medium;

$breakpoint-large-min: 	($breakpoint-medium + 1);
$breakpoint-large-max: 	$breakpoint-large;

$breakpoint-xl-min: 	  ($breakpoint-large + 1);

// Media Queries
// NOTE '~' in the beginning - media query must be escaped
// -----------------------------------------------------------------------------

$screen: 				  "only screen";

$small-up: 				"${screen}";
$small-only: 			"${screen} and (max-width: ${breakpoint-small-max})";
		
$medium-up: 			"${screen} and (min-width:${breakpoint-medium-min})";
$medium-only: 		"${screen} and (min-width:${breakpoint-medium-min}) and (max-width:${breakpoint-medium-max})";
		
$large-up: 				"${screen} and (min-width:${breakpoint-large-min})";
$large-only: 			"${screen} and (min-width:${breakpoint-large-min}) and (max-width:${breakpoint-large-max})";
		
$xl-up: 				  "${screen} and (min-width:${breakpoint-xl-min})";

// Maps
// ---------------------------------------------

$colors: (
  primary: 				$global-primary,
  secondary: 			$global-secondary,
  success: 				$global-success,
  warning: 				$global-warning,
  alert: 				  $global-alert
);

//$sizes: (
//        xs: $font-size-xs,
//        sm: $font-size-sm,
//        lg: $font-size-lg,
//        xl: $font-size-xl
//);

$states: (
  active: 				#{$namespace}active,
  disabled: 			#{$namespace}disabled
);

$global-radius: 	4px !default;
$global-rounded: 	1000px !default;

$radiuses: (
  radius: 				$global-radius,
  rounded: 				$global-rounded
);

// Components variables
// =====================================
$base-font-size: 						    100% !default;
$body-background: 						  #f9f9f9 !default;
$body-font-color: 						  $gray-darker !default;
$body-font-family: 						  $base-font-family !default;
$body-font-weight: 						  normal !default;
$body-font-style: 						  normal !default;

// list
$list: 									        #{$namespace}list !default;
$list-bg-active: 						    #ececec;
$list-color-title: 						  #454545;
$list-color-title-visited: 			#929292;
$list-color-info: 						  #a2a2a2;

// night
$night-bg: 								      #1f1f1f;
$night-loader-color: 					  #6e2a2a;

// list-night
$night-list-bg-active: 					#1a1a1a;
$night-list-color-title: 				#4e4e4e;
$night-list-color-title-visited:#343434;
$night-list-color-info: 				#343434;
// icon
$icon-color:                    #8e8e93;  
$icon-return:                   "\f614"; 
$icon-close:                    "\f60a";
$icon-search:                   "\f60c";
$icon-info-block:               "\f603";
$icon-success-block:            "\f604";
$icon-warn-block:               "\f605"; 
$icon-info:            					"\f606";
$icon-success:         					"\f607";
$icon-warn:            					"\f608";
$icon-close-page: 						  "\f60b";
//--1px--------  
$border-color: 							    #e0e0e0;
$border-radius-base: 					  4px;
$border-radius-lg: 						  8px;
$border-radius-max: 					  200px;
$z-index-border: 						    10;

// -- dialog----
$z-index-overlay: 						  9999;
$mask-bg: 								      rgba(0, 0, 0, .4);
$dialog-cnt-bg: 						    rgba(253, 253, 253, 0.95);
$dialog-cnt-width: 						  270px;
$dialog-bd-height-min: 				  71px;
$dialog-bd-padding: 					  18px;
$dialog-border-radius: 				  6px;
$dialog-hd-margin-bottom: 		  4px;
$dialog-ft-height: 						  42px;
$dialog-btn-active: 					  rgba(0, 0, 0, .1);
$dialog-close-color: 					  #828282;
$font-size-info: 						    16px;
$txt-link: 								      #00a5e0;
$line-height-bar: 						  44px;
$active-opacity: 						    .5;

//----toast------
$z-index-poptoast:               999;
$poptoast-height:                40px;
$poptoast-bg:                    rgba(0,0,0,.8);
$poptoast-border:                3px;
$txt-default:           				#000;
$toast-info:             				#0090ff;
$toast-success:          				#65d521;
$toast-warn:             				#f76249;

//--progress
$z-index-progress:              9; 
$progress-background-color: 		#e2e2e2;
$progress-bar-color:        		#65d521;