//toast.scss
//2016-11-28

.#{$ui-poptoast}{
    width: 100%;
    position: fixed;
    top: 0px;
    left: 0px;
    z-index: $z-index-poptoast;
    // padding: 0px 10px;
    box-sizing: border-box;
    // transition: all .5s;
    // -webkit-transition: all .5s
}

.#{$ui-poptoast}-cnt{
    background-color: $poptoast-bg;
    line-height: $poptoast-height;
    height: $poptoast-height; 
    color:#fff;
    font-size: $font-size-info;
    text-align: center;
    border-bottom-left-radius: $poptoast-border;
    border-bottom-right-radius: $poptoast-border;
    i{
        display: inline-block;
        width: 32px;
        height: 1px;
        vertical-align: top;
        &:before{
            @include icon;
            margin-right: 2px;
            margin-left: 4px;
            color: #fff;
            line-height: $poptoast-height;
        }
    }
    
}
.#{$ui-poptoast}-top{
    .#{$ui-poptoast}-cnt{
        @include nowrap;
    }
}
.#{$ui-poptoast}-middle{
    top: 38%;
    text-align:center;
    .#{$ui-poptoast}-cnt{
        height:auto;
        padding:10px;
        display: inline-block;
        border-radius: 5px;
        max-width:80%;
    }
}
.#{$ui-poptoast}-info i:before{
    content: $icon-info-block;
}
.#{$ui-poptoast}-success i:before{
    content: $icon-success-block;
}
.#{$ui-poptoast}-warn i:before{
    content: $icon-warn-block;
}

