// mixins.scss
// =============================================================================

// Clearfix
// @see http://nicolasgallagher.com/micro-clearfix-hack/
@mixin clearfix {
  &:before,
  &:after {
    content: " ";
    display: table;
  }

  &:after {
    clear: both;
  }
}

// Sizing
// -----------------------------------------------------------------------------
@mixin size($width, $height) {
  width: $width;
  height: $height;
}

@mixin square($size) {
  @include size($size, $size);
}

// TODO: add scrollableX & scrollableY
@mixin scrollable() {
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}

// Text
// -----------------------------------------------------------------------------

// Text overflow
// Requires inline-block or block for proper styling
@mixin text-overflow {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

// Line clamp
// http://dropshado.ws/post/1015351370/webkit-line-clamp
@mixin line-clamp($lines: 2, $line-height: 1.3em) {
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: $lines; // number of lines to show
  overflow: hidden;
  line-height: $line-height;
  max-height: $line-height * $lines;
}

// Form
// -----------------------------------------------------------------------------

// Disable OS-level styles
@mixin no-appearance {
  -webkit-appearance: none;
  -moz-appearance: none;
}

// Shape
// -----------------------------------------------------------------------------
/// CSS Triangle
/// Creates a CSS triangle, which can be used for dropdown arrows, popup tails, and more. Use this mixin inside a `&::before` or `&::after` selector, to attach the triangle to an existing element.
///
/// @param {number} $triangle-size - Width of the triangle.
/// @param {color} $triangle-color - Color of the triangle.
/// @param {keyword} $triangle-direction - Direction the triangle points. Can be `top`, `right`, `bottom`, or `left`.
@mixin css-triangle($triangle-size, $triangle-color, $triangle-direction) {
  content: "";
  display: block;
  width: 0;
  height: 0;
  border: inset $triangle-size;
  @if ($triangle-direction == top) {
    border-color: $triangle-color transparent transparent transparent;
    border-top-style: solid;
  }
  @if ($triangle-direction == bottom) {
    border-color: transparent transparent $triangle-color transparent;
    border-bottom-style: solid;
  }
  @if ($triangle-direction == left) {
    border-color: transparent transparent transparent $triangle-color;
    border-left-style: solid;
  }
  @if ($triangle-direction == right) {
    border-color: transparent $triangle-color transparent transparent;
    border-right-style: solid;
  }
}

// Retina borders
// @see https://gist.github.com/w0rm/c0dd4b7aed71acc03900
// @see http://dieulot.net/css-retina-hairline
// -----------------------------------------------------------------------------
@mixin retina-border-top($retina-border-color-fact: $retina-border-color) {
  position: relative;

  &::before {
    content: "";
    top: 0;
    left: 0;
    position: absolute;
    display: block;
    @include size(100%, 1px);
    background-color: $retina-border-color-fact;
  }

  // simulate a 1px border on retina screens
  @media (-webkit-min-device-pixel-ratio: 2) {
    &::before {
      transform: scaleY(0.5);
      transform-origin: 50% 0;
    }
  }
}

@mixin retina-border-bottom($retina-border-color-fact: $retina-border-color) {
  position: relative;

  &::after {
    content: "";
    bottom: 0;
    left: 0;
    position: absolute;
    display: block;
    @include size(100%, 1px);
    background-color: $retina-border-color-fact;
    backface-visibility: hidden;
  }

  // simulate a 1px border on retina screens
  @media (-webkit-min-device-pixel-ratio: 2) {
    &::after {
      transform: scaleY(0.5);
      transform-origin: 50% 100%;
    }
  }
}

// Animation
// -----------------------------------------------------------------------------

@mixin force-hardware-acceleration {
  transform: translateZ(0);
  backface-visibility: hidden;
  perspective: 1000;
}

%force-hardware-acceleration {
  @include force-hardware-acceleration();
}

// 1px----------------

@mixin border-radius($border-color-fact: $border-color,$border-radius: $border-radius-base,$border-before-radius: $border-radius-lg) {
  border: 1px solid $border-color-fact;
  border-radius: $border-radius;
  @media screen and (-webkit-min-device-pixel-ratio: 2) {
    position: relative;
    border: 0;
    &:before {
      content: "";
      width: 200%;
      height: 200%;
      position: absolute;
      top: 0;
      left: 0;
      border: 1px solid $border-color-fact;
      -webkit-transform: scale(.5);
      -webkit-transform-origin: 0 0;
      padding: 1px;
      -webkit-box-sizing: border-box;
      border-radius: $border-before-radius;
      pointer-events: none;
    }
  }
}

/* ui-1px  */
@mixin border($border-color-fact: $border-color) { //ä¼ ç©ºåé»è®¤é¢è² ä½ç½®ï¼top bottom left rightï¼
  // $border-t: gradient(linear, left bottom, left top, color-stop(.5, rgba(0, 0, 0, 0)), color-stop(.5, $border-color-fact));
  $border-t: -webkit-gradient(linear, left bottom, left top, color-stop(.5, rgba(0, 0, 0, 0)), color-stop(.5, $border-color-fact));
  $border-b: -webkit-gradient(linear, left top, left bottom, color-stop(.5, rgba(0, 0, 0, 0)), color-stop(.5, $border-color-fact));
  $border-l: -webkit-gradient(linear, right top, left top, color-stop(.5, rgba(0, 0, 0, 0)), color-stop(.5, $border-color-fact));
  $border-r: -webkit-gradient(linear, left top, right top, color-stop(.5, rgba(0, 0, 0, 0)), color-stop(.5, $border-color-fact));

  .#{$ui-border}-t {
    border-top: 1px solid $border-color-fact;
  }
  .#{$ui-border}-b {
    border-bottom: 1px solid $border-color-fact
  }
  .#{$ui-border}-tb {
    border-top: $border-color-fact 1px solid;
    border-bottom: $border-color-fact 1px solid;
    background-image: none;
  }
  .#{$ui-border}-l {
    border-left: 1px solid $border-color-fact;
  }
  .#{$ui-border}-r {
    border-right: 1px solid $border-color-fact;
  }
  .#{$ui-border} {
    border: 1px solid $border-color-fact;
  }
  .#{$ui-border}-radius {
    @include border-radius($border-color-fact);
  }
  @media screen and (-webkit-min-device-pixel-ratio: 2) {
    .#{$ui-border} {
      position: relative;
      border: 0;
    }
    .#{$ui-border}-t, .#{$ui-border}-b, .#{$ui-border}-l, .#{$ui-border}-r, .#{$ui-border}-tb {
      border: 0;
    }
    .#{$ui-border}-t {
      background-position: left top;
      background-image: $border-t;
    }
    .#{$ui-border}-b {
      background-position: left bottom;
      background-image: $border-b;
    }
    .#{$ui-border}-t,
    .#{$ui-border}-b,
    .#{$ui-border}-tb {
      background-repeat: repeat-x;
      -webkit-background-size: 100% 1px;
    }
    .#{$ui-border}-tb {
      background-image: $border-t, $border-b;
      background-position: top, bottom;
    }
    .#{$ui-border}-l {
      background-position: left top;
      background-image: $border-l;
    }
    .#{$ui-border}-r {
      background-position: right top;
      background-image: $border-r;
    }
    .#{$ui-border}-l,
    .#{$ui-border}-r {
      background-repeat: repeat-y;
      -webkit-background-size: 1px 100%;
    }
    .#{$ui-border}:after {
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background-image: $border-t, $border-r, $border-b, $border-l;
      -webkit-background-size: 100% 1px, 1px 100%, 100% 1px, 1px 100%;
      background-size: 100% 1px, 1px 100%, 100% 1px, 1px 100%;
      background-repeat: no-repeat;
      background-position: top, right, bottom, left;
      padding: 1px;
      -webkit-box-sizing: border-box;
      z-index: $z-index-border;
      pointer-events: none;
    }
  }
}
// nowrap
@mixin nowrap{
    max-width: 100%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
//  icon 
@mixin icon {
  font-family: "iconfont" !important;
  font-size: 32px;
  line-height: $line-height-bar;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  display: block;
  color: rgba(0, 0, 0, .5);
}
//   progress
@mixin progress($progress-background-color-fact:$progress-background-color, $progress-bar-color-fact:$progress-bar-color){
  
  .#{$ui-progress}{
      overflow: hidden;
      width: 100%;
      height: 2px;
      font-size: 0px;
      background-color: $progress-background-color-fact;
      box-sizing: border-box;
  }
  .#{$ui-progress} span{
      display: block;
      width: 0%;
      background: $progress-bar-color-fact;
      height: 100%;
      font-size: 0;
  }
}

